body {
  background: #fff;
  position: relative;
  overflow-x: hidden;
  font-family: "Mulish", sans-serif;
}

.container {
  padding: 0 60px;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
}

.btn__red {
  width: 100%;
  padding: 50px 0;
  background: #d50038;
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  border: none;
}
.btn__red b {
  display: block;
  position: relative;
  overflow: hidden;
}
.btn__red i {
  width: 100%;
  display: block;
  position: relative;
}
.btn__red i + i {
  position: absolute;
  left: 0;
  top: 100%;
}
.btn__red span {
  display: inline-block;
  position: relative;
  transition: transform 0.6s cubic-bezier(0.76, 0, 0.24, 1);
}
.btn__red:hover span {
  transform: translateY(-100%);
}
.btn__trans {
  width: 100%;
  padding: 37px 48px;
  background: transparent;
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #512d18;
  border: none;
  border: 1px solid rgba(69, 69, 69, 0.3);
}
.btn__trans b {
  display: block;
  position: relative;
  overflow: hidden;
}
.btn__trans i {
  width: 100%;
  display: block;
  position: relative;
}
.btn__trans i + i {
  position: absolute;
  left: 0;
  top: 100%;
}
.btn__trans span {
  display: inline-block;
  position: relative;
  transition: transform 0.6s cubic-bezier(0.76, 0, 0.24, 1);
}
.btn__trans:hover span {
  transform: translateY(-100%);
}

.nav {
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.nav__ham,
.nav .btn__trans {
  display: none;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 100%;
  max-width: 160px;
}
.nav__logo img {
  width: 100%;
}
.nav__list {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.nav__list a {
  font-weight: 500;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  position: relative;
}
.nav__list a::before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  top: calc(100% + 5px);
  height: 1px;
  background: #fff;
  opacity: 0.8px;
  transition: all 0.3s;
  right: 0;
}
.nav__list a:hover::before {
  width: 100%;
  left: 0;
}
.nav__block {
  display: grid;
  height: 120px;
  display: grid;
  grid-template-columns: auto 180px;
  align-items: center;
  position: relative;
}
.nav__block::before {
  content: "";
  position: absolute;
  right: 180px;
  height: 100%;
  background: #fff;
  opacity: 0.3;
  width: 1px;
}
.nav__block a {
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  padding: 0 50px;
  position: relative;
}
.nav__block a::before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  top: calc(100% + 5px);
  height: 1px;
  background: #fff;
  opacity: 0.8px;
  transition: all 0.3s;
  right: 50px;
}
.nav__block a:hover::before {
  width: calc(100% - 100px);
  left: 50px;
}
.nav__lang {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  backdrop-filter: blur(9px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #fff;
  cursor: pointer;
  margin: 0 auto;
  transition: all 0.3s;
}
.nav__lang:hover {
  transform: scale(1.1);
}
.nav__lang ul {
  display: none;
}
.nav__lang p {
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}

.header {
  transition: all 0.3s;
}
.header .nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
}
.header .nav .container {
  padding-right: 0;
}
.header__slide {
  position: relative;
  overflow: hidden;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 480px;
}
.header__aside {
  backdrop-filter: blur(29px);
  background: rgba(242, 163, 113, 0.1);
  padding: 217px 0px 0px;
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: all 0.3s;
}
.header__aside img {
  width: 276px;
  display: block;
  margin: 0 auto;
}
.header__aside p {
  margin: 40px 0 0;
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 1;
  text-align: center;
  color: #fff;
}
.header__aside p + p {
  margin: 10px 0 0;
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  opacity: 0.8;
}
.header__aside .btn__red {
  margin: 100px 0 0;
}
.header__dots {
  margin: 40px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.header__dots i {
  width: 0%;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  background: #fff;
  transition: all 0s;
}
.header__dots span {
  width: 40px;
  height: 1px;
  background: rgba(255, 255, 255, 0.3);
  position: relative;
}
.header__dots span.active i {
  width: 100%;
  transition: width 4s;
}
.header__dots span.fix i {
  width: 100%;
  transition-duration: 0s;
}
.header__bg {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  left: 0;
  top: 0;
  transition: all 1s;
  transform: translateX(0);
}
.header__info {
  padding-top: 200px;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.header__info h1,
.header__info h2 {
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 100px;
  line-height: 1;
  color: #fff;
}
.header__info p {
  margin: 30px 0 0;
  max-width: 515px;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.3;
  color: #fff;
}
.header .swiper-button-next,
.header .swiper-button-prev {
  left: 60px;
  width: 72px;
  height: 72px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(9px);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  transform: translateY(-50%);
  top: 50%;
  transition: all 0.3s;
}
.header .swiper-button-next svg,
.header .swiper-button-prev svg {
  width: 40px;
  height: 40px;
}
.header .swiper-button-next::after,
.header .swiper-button-prev::after {
  content: none;
}
.header .swiper-button-next:hover,
.header .swiper-button-prev:hover {
  transform: translateY(-50%) scale(1.1);
}
.header .swiper-button-next {
  left: auto;
  right: 540px;
}
.header .swiper-slide-active .header__aside {
  transform: translateX(0);
  transition-delay: 1s;
}
.header .swiper-slide-prev .header__bg {
  transform: translateX(50%);
}
.header .swiper-slide-next .header__bg {
  transform: translateX(-50%);
}

.title p {
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1;
  color: #c59a56;
}
.title h2 {
  margin: 10px 0 0;
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 76px;
  line-height: 1;
  color: #512d18;
}

.mission {
  padding: 120px 0 0;
}
.mission .title {
  max-width: 700px;
}
.mission__title {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: end;
}
.mission__title_text p {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  color: #6d6d6d;
}
.mission__title_text .btn__trans {
  width: auto;
  margin: 20px 0 0;
}
.mission__video {
  margin: 60px -60px 0;
  position: relative;
}
.mission__video video {
  width: 100%;
  display: block;
}
.mission__play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(9px);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
}
.mission__play:hover {
  transform: translate(-50%, -50%) scale(1.1);
}
.mission__mute {
  top: 75px;
  right: 60px;
  width: 72px;
  height: 72px;
  position: absolute;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(9px);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
}
.mission__mute:hover {
  transform: scale(1.1);
}

.set {
  position: relative;
  overflow: hidden;
}
.set__block {
  max-width: calc(100% - 460px);
  padding: 120px 0 0;
}
.set__tab {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
}
.set__tab_point {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(69, 69, 69, 0.3);
  padding: 10px 20px;
  gap: 20px;
  cursor: pointer;
  transition: all 0.3s;
}
.set__tab_point img {
  width: 56px;
  height: 56px;
  border-radius: 100%;
}
.set__tab_point p {
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #6d6d6d;
  max-width: 150px;
  transition: all 0.3s;
}
.set__tab_point:hover, .set__tab_point.active {
  background: #d50038;
  border: 1px solid #d50038;
}
.set__tab_point:hover p, .set__tab_point.active p {
  color: #fff;
}
.set__scroll {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  display: none;
  margin: 40px 0 0;
}
.set__scroll + .btn__red {
  display: none;
}
.set__wrapper.active .set__scroll {
  display: grid;
}
.set__wrapper_width .set__scroll {
  grid-template-columns: 1fr 1fr;
}
.set__item {
  position: relative;
  padding: 40px;
  z-index: 2;
  background: url("../img/set_bg.jpg") center no-repeat;
  background-size: cover;
  transition: all 0.3s;
}
.set__item::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  background: url("../img/set_bg_hover.jpg") center no-repeat;
  opacity: 0;
  transition: all 0.3s;
  top: 0;
  left: 0;
}
.set__item img {
  width: 234px;
  display: block;
  transition: all 0.3s;
  margin: 0 auto;
  pointer-events: none;
}
.set__item_name {
  margin: 40px 0 0;
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 1;
  text-align: center;
  color: #512d18;
  transition: all 0.3s;
}
.set__item_fav {
  position: absolute;
  top: 20px;
  right: 20px;
}
.set__item_fav p {
  text-align: center;
  margin: 5px 0 0;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  color: #512d18;
  transition: all 0.3s;
}
.set__item_fav svg {
  margin: 0 auto;
  display: block;
}
.set__item_fav svg path {
  transition: all 0.3s;
}
.set__item_text {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  color: #fff;
  opacity: 0.8;
  position: relative;
  transition: all 0.3s;
  max-height: 0;
  overflow: hidden;
  margin: 0;
}
.set__item_har {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  position: relative;
  transition: all 0.3s;
  max-height: 0;
  overflow: hidden;
  margin: 0;
}
.set__item_har p {
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  color: #fff;
}
.set__item_har span {
  display: block;
  width: 1px;
  height: 20px;
  background: #fff;
}
.set__item:hover::before {
  opacity: 1;
}
.set__item:hover .set__item_fav path {
  fill: #fff;
}
.set__item:hover .set__item_fav p {
  color: #fff;
}
.set__item:hover .set__item_name {
  margin: 30px 0 0;
  color: #fff;
}
.set__item:hover .set__item_text {
  max-height: 38px;
  margin: 5px 0 0;
}
.set__item:hover .set__item_har {
  margin: 20px 0 0;
  max-height: 20px;
}
.set__item:hover img {
  width: 187px;
}
.set__aside {
  position: absolute;
  overflow: hidden;
  top: 0;
  right: 0px;
  height: 100%;
  width: 480px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: all 0.5s;
  transform: translateX(100%);
  max-height: 898px;
}
.set__aside img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
  transform: translateX(-50%);
  transition: all 0.5s;
}
.set__aside_info {
  backdrop-filter: blur(29px);
  background: rgba(242, 233, 113, 0.1);
  padding: 40px;
}
.set__aside_info p {
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 56px;
  line-height: 1;
  color: #fff;
}
.set__aside_info p + p {
  margin: 20px 0 0;
  font-family: "Mulish", sans-serif;
  font-size: 28px;
  line-height: 1.3;
}
.set__aside.active {
  transform: translateX(0%);
  transition-delay: 0.3s;
}
.set__aside.active img {
  transform: translateX(0%);
  transition-delay: 0.3s;
}
.set__aside_fix {
  max-height: 898px;
  height: 898px;
}
.set__aside_fix.fix {
  position: fixed;
  top: 0;
  right: 0;
}
.set__aside_fix.bottom {
  top: auto;
  bottom: 0;
}

.blog {
  padding: 120px 0;
  position: relative;
  overflow: hidden;
}
.blog__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  margin: 40px 0 0;
}
.blog__main {
  height: 100%;
  z-index: 2;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  overflow: hidden;
}
.blog__main img {
  position: absolute;
  z-index: -1;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s;
}
.blog__main_info {
  padding: 40px;
  backdrop-filter: blur(29px);
  background: rgba(143, 101, 77, 0.1);
}
.blog__main p {
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 56px;
  line-height: 1;
  color: #fff;
}
.blog__main p + p {
  margin: 20px 0 0;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.3;
  color: #fff;
}
.blog__main:hover img {
  transform: scale(1.2);
}
.blog__block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
.blog__item {
  z-index: 2;
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-rows: 280px 1fr;
}
.blog__item img {
  height: 280px;
  object-fit: cover;
  width: 100%;
  z-index: -1;
  transition: all 0.3s;
  position: relative;
}
.blog__item_info {
  border: 1px solid rgba(69, 69, 69, 0.3);
  padding: 40px;
  z-index: 2;
  background: #fff;
  position: relative;
  height: 100%;
}
.blog__item_info p {
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 1;
  color: #512d18;
}
.blog__item_info p + p {
  margin: 15px 0 0;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: #6d6d6d;
  opacity: 0.8;
}
.blog__item:hover img {
  transform: scale(1.2);
}
.blog .btn__trans {
  width: auto;
  margin: 40px auto 0;
  display: block;
}

.input input,
.input textarea {
  display: block;
  border: none;
  width: 100%;
  box-sizing: border-box;
  padding: 20px 0;
  border-bottom: 1px solid rgba(81, 45, 24, 0.3);
  font-family: "Mulish", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #512d18;
  transition: all 0.3s;
}
.input input:placeholder-shown,
.input textarea:placeholder-shown {
  color: #512d18;
}
.input input:focus,
.input textarea:focus {
  border-bottom: 1px solid #512d18;
}
.input textarea {
  height: 82px;
  resize: none;
}
.input__check {
  display: grid;
  grid-template-columns: 16px 1fr;
  gap: 10px;
  align-items: start;
  position: relative;
}
.input__check span {
  border: 1px solid rgba(81, 45, 24, 0.3);
  width: 16px;
  height: 16px;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.input__check p {
  transition: all 0.3s;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: rgba(81, 45, 24, 0.5);
}
.input__check p a {
  color: #d50038;
}
.input__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  z-index: 2;
  opacity: 0;
  cursor: pointer;
}
.input__check input:hover + span {
  border: 1px solid #512d18;
}
.input__check input:hover + span + p {
  color: #512d18;
}
.input__check input:checked + span {
  border: 1px solid #d50038;
}
.input__check input:checked + span svg {
  opacity: 1;
}
.input__check input:checked + span + p {
  color: #512d18;
}

.form {
  position: relative;
  padding: 500px 0 0;
  z-index: 2;
  overflow: hidden;
}
.form__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.form__block {
  max-width: 880px;
  margin: 0 auto;
  background: #fff;
  padding: 60px 60px 0;
  transform: translateY(100%);
  transition: all 0.7s;
  position: relative;
  opacity: 0.5;
}
.form__block.active {
  transform: translateY(0%);
  opacity: 1;
}
.form h2 {
  font-family: "Philosopher", sans-serif;
  font-weight: 400;
  font-size: 76px;
  line-height: 1;
  color: #512d18;
}
.form h2 + p {
  margin: 20px 0 0;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.3;
  color: #6d6d6d;
  max-width: 650px;
}
.form__wrapper {
  margin: 36px 0 0;
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}
.form__wrapper .input:nth-child(3) {
  grid-column: 1/3;
}
.form__wrapper .input__check {
  grid-column: 1/3;
}
.form .btn__red {
  margin: 60px -60px 0;
  width: calc(100% + 120px);
}

.footer {
  background: #1d1b1a;
  padding: 60px 0 30px;
}
.footer__wrapper {
