    line-height: 1.2;
  }
  .header__dots {
    margin: 20px 0 0;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 10px;
  }
  .header__dots span {
    width: 100%;
  }
  .header .swiper-button-prev,
  .header .swiper-button-next {
    display: none;
  }
  .header__info {
    max-width: 100%;
  }
  .header__info h1,
  .header__info h2 {
    width: 100%;
    font-size: 42px;
  }
  .header__info p {
    font-size: 18px;
    margin: 20px 0 0;
  }
  .title h2 {
    font-size: 40px;
    margin: 10px 0 0;
  }
  .title p {
    font-size: 16px;
  }
  .mission {
    padding: 60px 0 0;
  }
  .mission__title {
    gap: 20px;
  }
  .mission__title_text p {
    font-size: 16px;
  }
  .mission__video {
    margin: 30px -20px 0;
    width: calc(100% + 40px);
  }
  .mission__video video {
    height: 400px;
    object-fit: cover;
  }
  .mission__play, .mission__mute {
    width: 50px;
    height: 50px;
  }
  .mission__play svg {
    width: 32px;
    height: 32px;
  }
  .mission__mute {
    top: 20px;
    right: 20px;
  }
  .mission__mute svg {
    width: 24px;
    height: 24px;
  }
  .set__block {
    padding: 60px 0 0;
  }
  .set__aside {
    padding: 158px 0 0;
    margin: 30px -20px 0;
    width: calc(100% + 40px);
  }
  .set__aside_info {
    max-width: 100%;
    padding: 30px 20px;
  }
  .set__aside_info p {
    font-size: 30px;
  }
  .set__aside_info p + p {
    margin: 10px 0 0;
    font-size: 16px;
  }
  .set__item {
    width: calc(100vw - 120px);
    min-width: calc(100vw - 120px);
    padding: 30px 10px !important;
  }
  .set__item img {
    width: 113px !important;
  }
  .set__item_text, .set__item_har {
    display: none;
  }
  .set__item_name {
    margin: 20px 0 0 !important;
    font-size: 22px;
  }
  .set__item_fav {
    top: 10px;
    right: 10px;
  }
  .set__item_fav svg {
    width: 24px;
    height: 24px;
  }
  .set__item_fav p {
    font-size: 12px;
    margin: 0;
  }
  .set__tab {
    margin: 30px -20px 0;
    width: calc(100% + 40px);
    padding: 0 20px;
  }
  .blog {
    padding: 60px 0;
  }
  .blog__main {
    margin: 0 -20px;
    width: calc(100% + 40px);
    padding: 166px 0 0;
  }
  .blog__main_info {
    width: 100%;
    padding: 30px 20px;
  }
  .blog__main_info p {
    font-size: 30px;
  }
  .blog__main_info p + p {
    margin: 10px 0 0;
    font-size: 14px;
  }
  .blog__item {
    width: calc(100vw - 120px);
    min-width: calc(100vw - 120px);
    grid-template-rows: 180px 1fr;
  }
  .blog__item img {
    height: 180px;
  }
  .blog__item_info {
    padding: 30px 20px;
  }
  .blog__item_info p {
    font-size: 22px;
  }
  .blog__item_info p + p {
    margin: 10px 0 0;
    font-size: 14px;
  }
  .input input,
  .input textarea {
    padding: 0 0 20px;
    font-size: 12px;
  }
  .input textarea {
    height: 60px;
  }
  .form__bg {
    height: 300px;
  }
  .form__block {
    padding: 30px 0 0;
  }
  .form h2 {
    font-size: 40px;
  }
  .form h2 + p {
    font-size: 18px;
  }
  .form__wrapper {
    margin: 20px 0 0;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .form__wrapper .input:nth-child(3),
  .form__wrapper .input__check {
    grid-column: auto;
  }
  .form .btn__red {
    margin: 30px -20px 0;
    width: calc(100% + 40px);
  }
  .footer {
    padding: 40px 0 20px;
    position: relative;
  }
  .footer__wrapper {
    grid-template-columns: 100%;
    gap: 30px;
    position: relative;
  }
  .footer__wrapper * {
    grid-column: auto;
    grid-row: auto;
  }
  .footer__serv {
    order: 2;
  }
  .footer__serv ul {
    margin: 0;
    justify-items: start;
  }
  .footer__serv ul a {
    text-align: left;
  }
  .footer__up {
    position: absolute;
    top: 0;
    right: 0;
  }
  .footer__logo {
    max-width: 90px;
  }
  .footer__list a {
    font-size: 14px;
  }
  .footer__contact p {
    font-size: 14px;
  }
  .footer__contact a {
    font-size: 14px;
  }
  .footer__contact div + div {
    margin: 10px 0 0;
  }
  .footer__sub p {
    font-size: 12px;
  }
}